import { Button } from 'antd'
import React, { useEffect, useState } from 'react'

type Op = '+' | '-' | '*' | '/'
interface CalcItem {
  left: number
  right: number
  op: Op
}

const ops: Op[] = ['+', '-', '*', '/']

const TestView = () => {
  useEffect(() => {
    for (let i = 0; i < 70; i++) {
      randomCreate()
    }
  }, [])
  const [items, setItems] = useState<CalcItem[]>([])

  const randomCreate = () => {
    const op = ops[getNum(3)]
    let left = 0
    let right = 0
    if (op === '*') {
      left = getTenNumber()
      right = getCNumer()()
      console.log(left, right)
    } else {
      left = getNum(10)
      right = getNum(10)
    }
    setItems((items) => items.concat([{ op, left, right }]))
  }

  const getNum = (range: number) => {
    return Math.round(Math.random() * (range - 1)) + 1
  }

  const getTenNumber = () => {
    return getNum(9) * [10, 100][getNum(1)]
  }

  const getCNumer = () => {
    return [getTenNumber, () => getNum(99)][getNum(1)]
  }

  return (
    <>
      <div>
        <div style={{ padding: '10px' }}>
          <div style={{ fontSize: '20px' }}>计算题生成</div>
          <div style={{ display: 'flex', flexWrap: 'wrap', border: '1px solid #666' }}>
            {items.map((i, idx) => {
              return (
                <div
                  style={{ fontSize: '16px', display: 'flex', margin: '10px', flex: '10% 0 0' }}
                  key={idx}
                >
                  <div>{i.left}</div>
                  <div style={{ margin: '0 5px' }}>{i.op}</div>
                  <div style={{ marginRight: '5px' }}>{i.right}</div>

                  {i.left % i.right !== 0 ? '=' : '~='}
                </div>
              )
            })}
          </div>
        </div>
        <div>{/* <Button onClick={() => randomCreate()}>生成</Button> */}</div>
      </div>
    </>
  )
}
export default TestView
